@-webkit-keyframes arrow{
0%{opacity:0;
-webkit-transform:translateY(-20px)}
100%{opacity:1;
-webkit-transform:translateY(0)}
}
@-moz-keyframes arrow{
0%{opacity:0;
-moz-transform:translateY(-20px)}
100%{opacity:1;
-moz-transform:translateY(0)}
}



@media (max-width: 1199px) {
	.w1000 { 
		max-width: 1000px; 
		min-width: 980px;
		width: auto;
	}
	.w1200 { 
		max-width: 1200px; 
		min-width: 980px;
		width: auto;
	}
	.index-page .banner .list li .item .btn-item{
		padding: 40px 0 0 42%;
	}
	.index-page .use-step-item .use-step-con{
		background-size: contain;
	}
}